  padding: 0;
  margin: 0;
}
 
body {
  font-family: 'Poppins', sans-serif;
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
 
 
/*
  AppBar
*/
 
.app-bar {
  padding: 8px 16px;
  background-color: white;
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}
 
.app-bar .app-bar__menu {
  display: flex;
  align-items: center;
}
 
.app-bar .app-bar__menu button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
  min-width: 44px; 
  min-height: 44px; 
}
 
.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
}
 
.app-bar .app-bar__brand h1 {
  color: #6DC5D1;
  text-transform: uppercase;
  font-size: 22px;
  user-select: none;
  min-width: 44px; 
  min-height: 44px; 
}
 
.app-bar .app-bar__navigation {
  position: absolute;
  top: 50px;
  left: -180px;
  width: 150px;
  transition: all 0.3s;
  padding: 8px;
  background-color: white;
  overflow: hidden;
}
 
.app-bar .app-bar__navigation.open {
  left: 0;
}
 
.app-bar .app-bar__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: black;
  padding: 8px;
  margin-bottom: 5px;
  width: 100%;
  min-width: 44px; 
  min-height: 44px; 
}

.hero {
  position: relative;
  min-height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  text-align: center;
  overflow: hidden; 
}

.hero img.hero-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1; 
}
.hero-content {
  z-index: 1;
}

.hero-content h1{
  background-color: #DD761C;
  padding: 1px;
}
.hero-content p{
font-weight: bold;
text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}
.hero-content #napp{
  text-decoration: underline;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}
 
/*
  Main Content
*/
 
main {
  padding: 32px;
  flex: 1;
}
 
.content {
  margin: 0 auto;
  min-height: 100%;
}
 
.content .content__heading {
  font-weight: normal;
}
 
 
/*
  Restaurants
*/
 
.restaurants {
  display: grid;
  grid-template-columns: 1fr;
  gap: 16px;
  margin: 32px 0;
}
 
 
/*
  Restaurant Item
*/
 
.restaurant-item {
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  overflow: hidden;
}
 
.restaurant-item__header {
  position: relative;
}
 
.restaurant-item .restaurant-item__header .restaurant-item__header__poster {
  width: 100%;
}
 
.restaurant-item .restaurant-item__header .restaurant-item__header__rating {
  position: absolute;
  padding: 8px;
  bottom: 20px;
  left: 0;
  display: inline-block;
  background-color: black;
  color: white;
}
 
.restaurant-item .restaurant-item__header .restaurant-item__header__rating .restaurant-item__header__rating__score {
  margin-left: 10px;
}
 
.restaurant-item .restaurant-item__content {
  padding: 16px;
}
 
.restaurant-item .restaurant-item__content h3 {
  margin: 0 0 10px 0;
}
 
.restaurant-item .restaurant-item__content h3 a {
  color: #6DC5D1;
  text-decoration: none;
  display: inline-block;

  min-width: 44px;
  min-height: 44px;
}
 
.restaurant-item .restaurant-item__content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4; /* number of lines to show */
  -webkit-box-orient: vertical;
}

 
/*
  Restaurant
*/
 
.restaurant {
  margin: 0 auto;
  width: 100%;
  max-width: 800px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 18px 16px;
}
 
.restaurant .restaurant__poster {
  width: 100%;
  max-width: 400px;
}
 
.restaurant .restaurant__info h4 {
  margin: 8px 0;
}

/*
  Restaurant Detail
*/
 
.restaurant-detail {
  margin: 0 auto;
  width: 100%;
  max-width: 800px;
  display: grid;
  gap: 18px 16px;
}
 
.restaurant-detail .restaurant-detail__poster {
  width: 100%;
  max-width: 400px;
}
 
.restaurant-detail .restaurant-detail__info h4 {
  margin: 8px 0;
}
 
.restaurant-detail .restaurant-detail__menu {
  margin-top: 24px;
}
 
.restaurant-detail .restaurant-detail__menu h3 {
  margin-bottom: 12px;
}
 
.restaurant-detail .restaurant-detail__menu ul {
  list-style-type: none;
  padding: 0;
}

.restaurant-detail .restaurant-detail__menu ul li {
  margin-bottom: 8px;
}

.restaurant-detail .restaurant-detail__reviews {
  margin-top: 24px;
}

.restaurant-detail .restaurant-detail__reviews ul {
  list-style-type: none;
  padding: 0;
}

.restaurant-detail .restaurant-detail__reviews ul li {
  margin-bottom: 12px;
}

.restaurant-detail .restaurant-detail__reviews ul li span {
  font-weight: bold;
}

.restaurant__add-review {
  margin-top: 20px;
}

.restaurant__add-review form {
  max-width: 400px;
  margin-top: 10px;
}

.restaurant__add-review form label {
  margin-top: 10px;
  display: block;
  font-weight: bold;
}

.restaurant__add-review form input[type="text"],
.restaurant__add-review form textarea {
  width: 100%;
  padding: 8px;
  margin-top: 6px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  min-width: 44px;
  min-height: 44px;
}

.restaurant__add-review form button {
  background-color: #db0000;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
  min-width: 44px;
  min-height: 44px;
}

.restaurant__add-review form button:hover {
  background-color: #bf1722;
}

#query{
  padding: 5px;
  width: 100%;
  min-width: 44px;
  min-height: 44px;
}
.restaurant-item__not__found{
  text-align: center;
}

 
.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #DD761C;
  color: white;
  padding: 8px;
  z-index: 100;
  display: inline-block;
}

.skip-link:focus{
  top: 0;
}

.uc{
  border-bottom: 2px solid #6DC5D1;
  display: inline-block;
}
/*
  Like
*/
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #DD761C;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}
 
/*
  Footer
*/
 
footer {
  padding: 16px;
}
 
footer p {
  text-align: center;
  color: #aaaaaa;
}
 
footer p b{
  color: #DD761C;
  text-decoration: none;
